import React from "react";
import Box from "./scene";
import { Canvas } from "react-three-fiber";

function App() {
  return (
    <Canvas style={{ height: 800, width: 800 }}>
      <ambientLight />
      <pointLight position={[10, 10, 10]} />
      <Box position={[-2, 0, 0]} />
      <Box position={[2, 0, 0]} />
      <Box position={[0, 2, 0]} />
    </Canvas>
  );
}
export default App;
